
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'BOTable',
  inheritAttrs: false,
  setup(props) {
    const rowClassName = ({ rowIndex }: { rowIndex: number }) => {
      if ((rowIndex + 1) % 2 !== 0) {
        return 'odd-row'
      } else {
        return 'even-row'
      }
    }
    return { rowClassName }
  }
})
