
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'BOTabs',
  inheritAttrs: false,
  props: {
    type: {
      type: String,
      default: ''
    }
  },
  setup(props) {
    const styleType = `bo-tabs-${props.type ? props.type : 'default'}`
    return { styleType }
  }
})
